import React from 'react';
import './Scroll.css';
import {Link} from "react-scroll";

class Scroll extends React.Component {
    constructor(props) {
        super(props);
    }


    render() {
        return (
            <Link
                to={this.props.href}
                spy={true}
                smooth={true}
                duration={500}
            >
                <a className={this.props.class} href={this.props.href}>
                    <img src={this.props.img} alt=""/>
                </a>
            </Link>
        )
    }

}

export default Scroll;
